/* Cards.css */
.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  height: auto;
  margin-top:70px;
}

.card {
  width: 350px;
  height: 550px;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #fff8dc;
  border: 2px solid #000;
  border-radius: 10px;
  box-shadow: 5px 5px 0 #000;
  margin: 20px;
  padding: 20px;

  transition: transform 1.5s ease-in-out, box-shadow 0.2s;
}

.card img {
  width: 100%;
  height: auto;
  border-radius: 10px 10px 0 0;
}

.marvel-enlace {
  position: absolute;
  bottom: 0;
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-size: 1em;
}

@media (max-width: 1024px) {
  .card-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .card-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .card-container {
    grid-template-columns: 1fr;
  }
  .marvel-enlace {
    font-size: 0.8em;
  }
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 10px 10px 0 #000;
  /*  transform: rotateY(360deg); */
  cursor: pointer;

  animation: rotate-scale-up-vertical 2.2s linear both;
}

@keyframes rotate-scale-up-vertical {
  0% {
    transform: scale(1) rotateY(0);
  }

  50% {
    transform: scale(0.7) rotateY(180deg);
  }

  100% {
    transform: scale(0.9) rotateY(360deg);
  }
}

.card-title {
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-size: 1.5em;
  color: #ff4500;
  text-shadow: 2px 2px #000;
  text-align: center;
}

.card-content {
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-size: 1em;
  color: #000;
  overflow-y: auto;
  text-align: justify;
  padding-right: 5px;
}

.card-button {
  background-color: #ff4500;
  border: none;
  border-radius: 5px;
  color: #fff;
  margin-bottom: 10px;
  padding: 10px 20px;
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.2s;
  box-shadow: 2px 2px #000;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
}

.card-button:hover {
  background-color: #ff6347;
}